import React, { useState } from "react";
import {
  Stack,
  Box,
  Heading,
  Card,
  CardBody,
  CardFooter,
  ResponsiveContext,
  Text,
} from "grommet";
import ProgressiveImage from "react-progressive-image";
import { navigate } from "gatsby";
import Modal from "../../components/Modal";
import FileGallery from "../../components/FileGallery";
import { Braille as Apps, Close } from "grommet-icons";
import { Image } from "../../components";
import { FALLBACK_IMAGE } from "../../constants";

const Component = ({
  dataList,
  onClickAppsIcon,
  showFallBack = false,
  isOwner,
  vertical,
  render,
}) => {
  const avatarSrc = "";
  const size = React.useContext(ResponsiveContext);
  const [showModal, setShowModal] = useState(false);
  const [activeCardImage, setActiveCardImage] = useState([]);

  const widthLookup = {
    xxsmall: "100%",
    xsmall: "100%",
    small: "100%",
    medium: "24%",
    xlarge: "20%",
    full: "20%",
  };
  return (
    <Box
      fill={true}
      wrap={true}
      direction={vertical ? "column" : "row"}
      gap="10px"
    >
      {dataList &&
        dataList.map((e, i) => {
          const { thumbnail, title, cta, cardHref, image, description } = e;
          const boxProps = cardHref
            ? {
                style: { cursor: "pointer" },
                onClick: () => navigate(cardHref),
              }
            : {};
          return (
            <Box
              {...boxProps}
              outline={false}
              key={i}
              width={widthLookup[size]}
              gap="xxsmall"
              direction={vertical ? "row" : "column"}
            >
              {render && render(e, i)}
            </Box>
          );
        })}
      {showModal && activeCardImage && (
        <Modal onClose={() => setShowModal(false)}>
          <Box>
            <FileGallery showUpload={isOwner} files={activeCardImage} />
          </Box>
        </Modal>
      )}
    </Box>
  );
};

export default Component;
