import React, { useEffect, useState } from "react";
import CardList from "../components/CardList/NewCardList";
import { Box, Image } from "grommet";
import { graphql } from "gatsby";
import styled from 'styled-components'
import { navigate } from 'gatsby'

const ImageContainer = styled.div`
  cursor: pointer;
`

function shuffle(array) {
  var currentIndex = array.length,  randomIndex;

  // While there remain elements to shuffle...
  while (0 !== currentIndex) {

    // Pick a remaining element...
    randomIndex = Math.floor(Math.random() * currentIndex);
    currentIndex--;

    // And swap it with the current element.
    [array[currentIndex], array[randomIndex]] = [
      array[randomIndex], array[currentIndex]];
  }

  return array;
}

const Component = ({ data, location }) => {
  const d = new Date();
  d.setMonth(d.getMonth() - 6);

  const [datasource, setDatasource] = useState([]);
  const [recentVideoDS, setRecentVideoDS] = useState([]);
  useEffect(() => {
    const newDS = data.allAggVideo.edges.map(({ node }) => {
      console.log(node);
      return {
        id: node.key,
        image: node?.thumbnails?.high?.url,
        title: node.title,
        publishTime: node.publishTime
      };
    });
    setDatasource(shuffle(newDS));
    setRecentVideoDS(newDS.filter(n => n.publishTime > d.toISOString()))
  }, [data.allAggVideo.edges]);
  return (
    <>
      {
        <>
          <Box alignContent="center" direction="row" justify="center"></Box>
          <Box alignContent="center" justify="center">
            OpenFengShui

            <Box>Recent Video</Box>
            <CardList
              dataList={recentVideoDS}
              render={(item, index) => {
                return <ImageContainer
                    onClick={() => {
                      navigate(`/video?id=${item.id}`)
                    }}
                  >
                  <Image
                    src={item.image}
                    imageProps={{
                      fit: "contain",
                    }}
                  />
                  <Box> { item.title } </Box>
                </ImageContainer>;
              }}
            />
            <Box>More Videos</Box>
            <CardList
              dataList={datasource}
              render={(item, index) => {
                return <ImageContainer
                    onClick={() => {
                      navigate(`/video?id=${item.id}`)
                    }}
                  >
                  <Image
                    src={item.image}
                    imageProps={{
                      fit: "contain",
                    }}
                  />
                  <Box> { item.title } </Box>
                </ImageContainer>;
              }}
            />
          </Box>
        </>
      }
    </>
  );
};

export default Component;


export const query = graphql`
query MyQuery11 {
  allAggVideo {
    edges {
      node {
        key
        title
        thumbnails {
          high {
            url
          }
        }
        publishedAt
        publishTime
      }
    }
  }
}


`